import React, { createContext, useContext, useEffect, useState, ReactNode } from 'react';
import { useQueryClient } from 'react-query';
import { io, Socket } from 'socket.io-client';
import { useAuth } from './AuthContext';

interface Message {
  _id: string;
  content: string;
  sender: {
    _id: string;
    username: string;
    avatar?: string;
  };
  room: string;
  type: string;
  createdAt: string;
  readBy: string[];
}

interface Room {
  _id: string;
  name: string;
  description?: string;
  type: string;
  members: any[];
  lastMessage?: Message;
  updatedAt: string;
}

interface OnlineUser {
  userId: string;
  username: string;
}

interface SocketContextType {
  socket: Socket | null;
  connected: boolean;
  messages: Message[];
  rooms: Room[];
  onlineUsers: OnlineUser[];
  currentRoom: string | null;
  sendMessage: (content: string, room: string) => void;
  joinRoom: (roomId: string) => void;
  leaveRoom: (roomId: string) => void;
  setCurrentRoom: (roomId: string | null) => void;
  markAsRead: (roomId: string) => void;
}

const SocketContext = createContext<SocketContextType | undefined>(undefined);

export const useSocket = () => {
  const context = useContext(SocketContext);
  if (context === undefined) {
    throw new Error('useSocket must be used within a SocketProvider');
  }
  return context;
};

interface SocketProviderProps {
  children: ReactNode;
}

const WS_URL = process.env.REACT_APP_WS_URL || (window.location.protocol === 'https:' ? 'wss:' : 'ws:') + '//' + window.location.host;

export const SocketProvider: React.FC<SocketProviderProps> = ({ children }) => {
  const { user, token } = useAuth();
  const queryClient = useQueryClient();
  const [socket, setSocket] = useState<Socket | null>(null);
  const [connected, setConnected] = useState(false);
  const [messages, setMessages] = useState<Message[]>([]);
  const [rooms, setRooms] = useState<Room[]>([]);
  const [onlineUsers, setOnlineUsers] = useState<OnlineUser[]>([]);
  const [currentRoom, setCurrentRoom] = useState<string | null>(null);
  
  

  useEffect(() => {
    if (user && token) {
      const newSocket = io(`${WS_URL}/chat`, {
        auth: {
          token,
        },
        transports: ['websocket'],
      });

      newSocket.on('connect', () => {
        console.log(' ');
        setConnected(true);
      });

      newSocket.on('disconnect', () => {
        console.log('  ');
        setConnected(false);
      });

      newSocket.on('newMessage', (message: any) => {
        setMessages(prev => [message as Message, ...prev]);
        //    :   messages    
        queryClient.setQueryData(['messages', message.room], (oldData: any) => {
          const prev = Array.isArray(oldData) ? oldData : [];
          const exists = prev.some((m: any) => m._id === message._id);
          if (exists) return prev;
          return [message, ...prev];
        });
        //    : lastMessage  updatedAt 
        queryClient.setQueryData('rooms', (oldData: any) => {
          if (!oldData || !Array.isArray(oldData)) return oldData;
          const updated = oldData.map((room: any) => {
            if (room._id !== message.room) return room;
            return {
              ...room,
              lastMessage: {
                ...(room.lastMessage || {}),
                content: message.content,
                sender: message.sender,
                senderUsername: message.senderUsername,
                createdAt: message.createdAt,
              },
              updatedAt: message.createdAt || new Date().toISOString(),
            };
          });
          //       
          updated.sort((a: any, b: any) => new Date(b.updatedAt).getTime() - new Date(a.updatedAt).getTime());
          return updated;
        });
      });

      // Handle initial online users list
      newSocket.on('onlineUsers', (users: OnlineUser[]) => {
        setOnlineUsers(users);
      });

      newSocket.on('userOnline', ({ userId, username }) => {
        setOnlineUsers(prev => {
          const filtered = prev.filter(user => user.userId !== userId);
          return [...filtered, { userId, username }];
        });
      });

      newSocket.on('userOffline', ({ userId }) => {
        setOnlineUsers(prev => prev.filter(user => user.userId !== userId));
      });

      newSocket.on('userJoinedRoom', ({ userId, roomId }) => {
        console.log(`User ${userId} joined room ${roomId}`);
      });

      newSocket.on('userLeftRoom', ({ userId, roomId }) => {
        console.log(`User ${userId} left room ${roomId}`);
      });

      newSocket.on('messagesRead', ({ userId, roomId }) => {
        setMessages(prev => prev.map(msg => 
          msg.room === roomId && !msg.readBy.includes(userId)
            ? { ...msg, readBy: [...msg.readBy, userId] }
            : msg
        ));
        
      });
      
      // (typing indicator removed)
      

      setSocket(newSocket);

      return () => {
        newSocket.close();
      };
    } else {
      if (socket) {
        socket.close();
        setSocket(null);
        setConnected(false);
      }
    }
  }, [user, token]);

  const sendMessage = (content: string, room: string) => {
    if (socket && connected) {
      socket.emit('sendMessage', {
        content,
        room,
        type: 'text',
      });
    }
  };

  const joinRoom = (roomId: string) => {
    if (socket && connected) {
      socket.emit('joinRoom', { roomId });
    }
  };

  const leaveRoom = (roomId: string) => {
    if (socket && connected) {
      socket.emit('leaveRoom', { roomId });
    }
  };

  const markAsRead = (roomId: string) => {
    if (socket && connected) {
      socket.emit('markAsRead', { roomId });
    }
  };

  const value = {
    socket,
    connected,
    messages,
    rooms,
    onlineUsers,
    currentRoom,
    sendMessage,
    joinRoom,
    leaveRoom,
    setCurrentRoom,
    markAsRead,
  };

  return (
    <SocketContext.Provider value={value}>
      {children}
    </SocketContext.Provider>
  );
};
