import React from 'react';
import {
  Box,
  Typography,
  List,
  ListItem,
  ListItemAvatar,
  ListItemText,
  Avatar,
  Badge,
  Chip,
  Divider,
  Fade,
} from '@mui/material';
import {
  Circle as CircleIcon,
  People as PeopleIcon,
} from '@mui/icons-material';

import { useSocket } from '../contexts/SocketContext';
import { useAuth } from '../contexts/AuthContext';

interface UserListProps {
  roomId: string | null;
}

const UserList: React.FC<UserListProps> = ({ roomId }) => {
  const { onlineUsers } = useSocket();
  const { user } = useAuth();

  const currentUser = user;
  //           
  const otherOnlineUsers = (onlineUsers || []).filter(
    (u: any) => u.userId !== currentUser?.id
  );
  const totalCount = (currentUser ? 1 : 0) + otherOnlineUsers.length;

  return (
    <Box sx={{ height: '100%', display: 'flex', flexDirection: 'column' }}>
      {/* Header */}
      <Box sx={{ 
        p: 3, 
        borderBottom: 1, 
        borderColor: 'divider',
        bgcolor: 'background.paper',
        backdropFilter: 'saturate(180%) blur(6px)'
      }}>
        <Box sx={{ display: 'flex', alignItems: 'center', gap: 2, mb: 2 }}>
          <PeopleIcon sx={{ color: 'primary.main' }} />
          <Typography variant="h6" sx={{ fontWeight: 600 }}>
             
          </Typography>
        </Box>
        <Chip 
          label={`${totalCount}  `} 
          size="small" 
          color="success" 
          variant="outlined"
          sx={{ 
            borderRadius: 2,
            fontWeight: 500
          }}
        />
      </Box>

      {/* User List */}
      <List sx={{ flex: 1, overflow: 'auto', p: 1 }}>
        {/* Current User */}
        {currentUser && (
          <Fade in timeout={300}>
            <ListItem dense sx={{ 
              mb: 1,
              bgcolor: 'primary.light',
              borderRadius: 2,
              color: 'primary.contrastText'
            }}>
              <ListItemAvatar>
                <Badge
                  overlap="circular"
                  anchorOrigin={{
                    vertical: 'bottom',
                    horizontal: 'right',
                  }}
                  badgeContent={
                    <CircleIcon 
                      sx={{ 
                        width: 12, 
                        height: 12,
                        color: 'success.main',
                        bgcolor: 'background.paper',
                        borderRadius: '50%'
                      }} 
                    />
                  }
                >
                  <Avatar sx={{ 
                    width: 36, 
                    height: 36,
                    bgcolor: 'primary.dark',
                    color: 'primary.contrastText'
                  }}>
                    {(currentUser.username || 'M')[0].toUpperCase()}
                  </Avatar>
                </Badge>
              </ListItemAvatar>
              <ListItemText
                primary={
                  <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
                    <Typography variant="subtitle2" sx={{ fontWeight: 600 }}>
                      {currentUser.username || 'Me'}
                    </Typography>
                    <Chip 
                      label="" 
                      size="small" 
                      sx={{ 
                        height: 18,
                        fontSize: '0.6rem',
                        bgcolor: 'primary.dark',
                        color: 'primary.contrastText'
                      }} 
                    />
                  </Box>
                }
                secondary={
                  <Typography variant="caption" sx={{ color: 'inherit', opacity: 0.8 }}>
                    
                  </Typography>
                }
              />
            </ListItem>
          </Fade>
        )}

        {/* Divider if current user exists and there are other users */}
        {currentUser && otherOnlineUsers.length > 0 && (
          <Divider sx={{ my: 1 }}>
            <Typography variant="caption" color="text.secondary">
               
            </Typography>
          </Divider>
        )}

        {/* Other Online Users */}
        {otherOnlineUsers.map((onlineUser: any, index: number) => (
          <Fade in key={onlineUser.userId || onlineUser._id || index} timeout={300} style={{ transitionDelay: `${index * 50}ms` }}>
            <ListItem dense sx={{ 
              mb: 0.5,
              borderRadius: 2,
              '&:hover': {
                bgcolor: 'action.hover'
              }
            }}>
              <ListItemAvatar>
                <Badge
                  overlap="circular"
                  anchorOrigin={{
                    vertical: 'bottom',
                    horizontal: 'right',
                  }}
                  badgeContent={
                    <CircleIcon 
                      sx={{ 
                        width: 10, 
                        height: 10,
                        color: 'success.main'
                      }} 
                    />
                  }
                >
                  <Avatar sx={{ 
                    width: 32, 
                    height: 32,
                    bgcolor: 'secondary.main',
                    fontSize: '0.875rem'
                  }}>
                    {(onlineUser.username || onlineUser.name || 'G')[0].toUpperCase()}
                  </Avatar>
                </Badge>
              </ListItemAvatar>
              <ListItemText
                primary={
                  <Typography variant="body2" sx={{ fontWeight: 500 }}>
                    {onlineUser.username || onlineUser.name || 'Guest'}
                  </Typography>
                }
                secondary={
                  <Typography variant="caption" color="success.main" sx={{ fontWeight: 500 }}>
                    
                  </Typography>
                }
              />
            </ListItem>
          </Fade>
        ))}
        
        {/* Empty State */}
        {otherOnlineUsers.length === 0 && !currentUser && (
          <Box sx={{ p: 3, textAlign: 'center' }}>
            <PeopleIcon sx={{ fontSize: 48, color: 'text.secondary', mb: 2 }} />
            <Typography variant="body2" color="text.secondary" gutterBottom>
                
            </Typography>
            <Typography variant="caption" color="text.secondary">
                  
            </Typography>
          </Box>
        )}

        {/* Only current user online */}
        {otherOnlineUsers.length === 0 && currentUser && (
          <Box sx={{ p: 2, textAlign: 'center' }}>
            <Typography variant="body2" color="text.secondary" gutterBottom>
                 
            </Typography>
            <Typography variant="caption" color="text.secondary">
                  
            </Typography>
          </Box>
        )}
      </List>

      {/* Footer Info */}
      <Box sx={{ 
        p: 2, 
        borderTop: 1, 
        borderColor: 'divider',
        bgcolor: 'background.paper'
      }}>
        <Typography variant="caption" color="text.secondary" align="center" display="block">
            
        </Typography>
      </Box>
    </Box>
  );
};

export default UserList;
